<template>
  <div class="p3">
    <h1 class="uk-text-center uk-margin-large-top uk-margin-large-bottom">固定资产加速折旧(扣除)优惠明细表</h1>
    <div style="margin: 0px auto; width: 1200px; text-align: left">
      <span>注:  <span style="width: 20px; height:10px; display:inline-block;background: #CCCCFF"></span> 不能填写 </span>
      <span>　　　金额单位：人民币元（列至角分）</span>
    </div>
    <form>
      <table cellpadding="0" cellspacing="0" border="1" width="100px">
        <tr>
          <td rowspan="3">行次</td>
          <td rowspan="3">项目</td>
          <td rowspan="2">资产值</td>
          <td colspan="5">本年累计折旧(扣除)金额</td>
        </tr>
        <tr>
          <td>账载折旧金额</td>
          <td>按照税收一般规定计算的折旧金额</td>
          <td>享受加速折旧优惠计算的折旧金额</td>
          <td>纳税调减金额</td>
          <td>享受加速折旧优惠金额</td>
        </tr>
        <tr>
          <td>1</td>
          <td>2</td>
          <td>3</td>
          <td>4</td>
          <td>5</td>
          <td>6(4-3)</td>
        </tr>
        <tr>
          <td>1</td>
          <td>一、固定资产加速折旧（不含一次性扣除，2+3）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_1" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_2" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_3" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_4" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_5" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="1_6" readonly/>
          </td>
        </tr>
        <tr>
          <td>2</td>
          <td>　　(一)重要行业固定资产加速折旧</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_1"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_2"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_3"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_4"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="2_5"/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="2_6" readonly/>
          </td>
        </tr>
        <tr>
          <td>3</td>
          <td>　　(二)其他行业研发设备加速折旧</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_1"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_2"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_3"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_4"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="3_5"/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="3_6" readonly/>
          </td>
        </tr>
        <tr>
          <td>4</td>
          <td>二、固定资产一次性扣除</td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_1"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_2"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_3"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_4"/>
          </td>
          <td style="background-color: #D6EBFF;">
            <input value="0.00" name="4_5"/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="4_6" readonly/>
          </td>
        </tr>
        <tr>
          <td>5</td>
          <td>合计（1+4）</td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_1" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_2" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_3" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_4" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_5" readonly/>
          </td>
          <td style="background-color: #CCCCFF;">
            <input value="0.00" name="5_6" readonly/>
          </td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 3,  //当前表格的名称编号
        setting: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn3(1, 5, [2, 3], 1)
        this.fn3(5, 5, [1, 4], 1)
        this.fn2([1, 2, 3, 4, 5], 6, [4, 3], 1)
      },
      // q  需计算单元格的所在行(数组)
      // r  需计算单元格的所在列
      // gump 参与计算的列
      // type 公式{
      //  1: 固定列所有行=gump[0]-gump[1]
      // }
      fn2(q, r, gump, type) {
        var sum = 0
        for (var k = 0; k < q.length; k++) {
          for (let i = 0; i < gump.length; i++) {
            let dom = J('input[name=' + q[k] + '_' + gump[i] + ']')
            if (dom.val()) {
              if (type == 1) {
                sum = parseFloat(J('input[name=' + q[k] + '_' + gump[0] + ']').val() * 1 - J('input[name=' + q[k] + '_' + gump[1] + ']').val() * 1)
              }
            }
          }
          J(' input[name=' + q[k] + '_' + r + ']').val(sum.toFixed(2))
        }
      },
      // q  要修改值的行
      // r  要修改多少列
      // seed 每列参与计算的行
      // type 运算类型
      // {
      //    1、 a+b+c+...
      //    2、 a-b-c-...
      // }
      // form_name 哪个form内的运算
      fn3(q, r, seed, type) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + m + '_' + i + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                if (type == 1) {
                  sum += parseFloat(dom.val())
                }
              }
            }
          }

          J('input[name=' + q + '_' + i + ']').val(sum.toFixed(2))
        }
      }

    }
  }
</script>

<style scoped>
  .p3 {
    text-align: center;
  }

  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-width: 1200px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
    margin: 0px auto;
  }

  input {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
  }

  .bg-gray {
    background: #ddd;
  }
</style>



